<template>
    <div class="chooseLeader">
        <div class="mainContentFluid">
            <div class="approval">
                {{title}}
            </div>
            <div class="fluidWidth">
                <div class="tripAppro">
                    <div class="name_circle">
                        <ul>
                            <li v-for="(item,index) in approver" :key="index">
                                <div class="lastname" v-bind:class="{'check':checkNum==index}" @click="leaderInfo(item,index)">
                                    {{item.empname.substring(0,1)}}
                                    <div class="nameClick">
                                    </div>
                                    <div class="imgClick">
                                    </div>
                                </div>
                                <div class="name" v-text="item.empname">
                                </div>
                            </li>
                        </ul>
                        <div class="clear"></div>
                    </div>
                </div>
            </div>
        </div>
        <!-- <div class="reason_sec">
                <div class="reasonTitle">填写附言</div>
                <trsTextarea placeholder="附言最多为100汉字..." v-model="remarks"></trsTextarea>
            </div> -->
    </div>
</template>
<script>
export default {
    name: 'selectLeaders',
    data() {
        return {
            checkNum: -1,
            leaderIDs: '',
            remarks: ''
        }
    },
    props: {
        approver: {
            type: Array
        },
        title: {
            type: String,
            default: '选择审批人'
        }
    },
    mounted() {
        // document.setTitle('快捷审批流程');
    },
    watch: {
        // checkNum: function() {
        //     let vm = this;
        //     vm.$emit('getCheckNum', vm.checkNum);
        // }
    },
    computed: {},
    methods: {
        leaderInfo(item, index) {
            let vm = this;
            vm.checkNum = index;
            vm.leaderIDs = item.operatorid;
            vm.$emit('getLeaderInfo', vm.leaderIDs, vm.checkNum);
        }
    }
}
</script>
<style lang="less" scoped>
.submit {
    background-color: #a4ceff;
}

.approval {
    height: .8rem !important;
    line-height: .8rem !important;
}

.name_circle ul li {
    margin-right: .03rem;
    margin-bottom: .2rem;
}

.chooseLeader {
    .mainContentFluid {
        margin-top: 0;

        .approval {
            height: 1rem;
            line-height: 1rem;
            color: #333;
            font-size: .32rem;
            padding-left: .38rem;
        }
    }

    .check .nameClick {
        width: 0.81rem;
        height: 0.81rem;
        margin: 0 auto;
        border-radius: 1rem;
        text-align: center;
        z-index: 2;
        opacity: 0.5;
        background-color: #256f7b;
        position: absolute;
        top: 0;
        right: 0;
    }

    .check .imgClick {
        width: 0.81rem;
        height: 0.81rem;
        margin: 0 auto;
        border-radius: 1rem;
        text-align: center;
        z-index: 99;
        position: absolute;
        top: 11px;
        right: -6px;
        background-image: url(../../../static/images/gou.png);
        background-repeat: no-repeat;
        background-size: 0.6rem;
    }

    .reason_sec {
        padding-left: .38rem;
        padding-right: .28rem;
        margin-top: 4px !important;
        min-height: 2rem;
        background-color: #fff;

        .sqReason {
            font-size: .32rem;
            color: #666;
            line-height: .6rem;
            min-height: 1rem;
            word-break: break-all;
            padding-bottom: .2rem;
        }

        .reasonTitle {
            height: .8rem;
            line-height: .8rem;
            font-size: .32rem;
            color: #333;
            font-weight: bold;
            padding-top: .05rem;
        }

        .trsTextarea {
            font-size: 0.32rem;
            padding-right: 0.2rem;
            color: #666;
            height: 2.1rem;
        }
    }
}
</style>


