<template>
    <yd-layout :class="'groundColor'">
        <selectLeaders :approver="approver" @getLeaderInfo="getLeaderInfo" v-if="approver.length!=0"></selectLeaders>
        <div class="assistBottom">
            <div class="examineLogo btnOne">
                <yd-flexbox>
                    <div class="examineLogo_txt toBack" @click="$router.go(-1)">返回</div>
                </yd-flexbox>
            </div>
            <div class="examineLogo btnTwo" v-on:click="send()">
                <yd-flexbox>
                    <div class="examineLogo_txt">发送</div>
                </yd-flexbox>
            </div>
        </div>
        <!-- <yd-tabbar slot="tabbar">
            <div class="assistBottom">
                <div class="examineLogo btnOne">
                    <yd-flexbox>
                        <div class="examineLogo_txt toBack" @click="$router.go(-1)">返回</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" v-on:click="send()">
                    <yd-flexbox>
                        <div class="examineLogo_txt">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar> -->
    </yd-layout>
</template>
<script>
import selectLeaders from '../selectLeader/selectLeader.vue';
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'lastSend',
    components: {
        selectLeaders
    },
    data() {
        return {
            queryTypeArr: [23, 24, 25],
            approver: [],
            checkNum: -1,
            leaderIDs: '',
            addFlag: false,
            sendFlag: true,
            formFlag: false,
            agencyFlag: false,
            formJson: {},
            secondFlag: this.$route.params.secondFlag === 'true' || this.$route.params.secondFlag === true
        }
    },
    mounted() {
        let vm = this;
        vm.approver = vm.getTravelInfo.approver;
        document.setTitle('出差审批');
    },
    computed: {
        ...mapGetters([
            'getUserInfo', 'getTravelInfo'
        ])
    },
    methods: {
        ...mapMutations([
            'setTravelInfo'
        ]),
        getLeaderInfo(leaderIDs, checkNum) {
            let vm = this;
            vm.leaderIDs = leaderIDs;
            vm.checkNum = checkNum;
        },
        send() {
            let vm = this;
            if (vm.checkNum == -1) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'travelremark',
                    'approveType': 0,
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': 'travelApplyActivity',
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.getTravelInfo.firstorgname + vm.getTravelInfo.confirmItem + '出差备案申请',
                    'ext': '{"applicantid":"' + vm.getTravelInfo.applicantid + '","applicantname":"' + vm.getTravelInfo.confirmItem + '","startdate":"' + vm.getTravelInfo.startdate + '","enddate":"' + vm.getTravelInfo.enddate + '","traveldays":"' + vm.getTravelInfo.traveldays + '","travelplace":"' + vm.getTravelInfo.travelplace + '","othertravelnames":"' + vm.getTravelInfo.othertravelnames + '","othertravelids":"' + vm.getTravelInfo.othertravelids + '","reason":"' + vm.getTravelInfo.reason + '","reason2":"' + vm.getTravelInfo.reason2 + '"}'
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.setTravelInfo({
                            formJson: {},
                            approver: [],
                            secretariesList: [],
                            applyList: [],
                            reason: '',
                            confirmItem: '',
                            startdate: '',
                            enddate: '',
                            applicantid: '',
                            traveldays: '',
                            travelplace: '',
                            othertravelnames: '',
                            selectothertravelnames: {},
                            reason2: '',
                            firstorgname: '',
                            applicantUserId: '',
                            statusRadio: 0,
                            jsonData: {}
                        })
                    }
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style scoped>
.check .nameClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 2;
    opacity: 0.5;
    background-color: #256f7b;
    position: absolute;
    top: 0;
    right: 0;
}

.check .imgClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 99;
    position: absolute;
    top: 11px;
    right: -6px;
    background-image: url(../../../static/images/dh.png);
    background-repeat: no-repeat;
    background-size: 0.6rem;
}

.name_circle {
    margin-top: 0.5rem;
}

.approval {
    font-size: 0.32rem;
    padding-left: 0.53rem;
    padding-top: 0.27rem;
    font-weight: normal;
    border-bottom: 1px dashed #eaeaea;
}

.mainContentFluid {
    margin-top: 0;
    height: 100%;
}

.groundColor {
    background-color: #fff;
}

.assistBottom {
    position: relative;
    border-top: none;
    padding: 0 .2rem;
    background-color: #fff;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
    border-radius: 5px;
    width: 48%;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
    float: right;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}
</style>


